@value Main, MainGrey, LightGrey, MainOrange from '../../common/colors.css';
@value wrapPadding, sectionTopBottom, MediaLaptop, MediaPad, MediaMobile, FontSmall, FontRegular, FontMedium , Title from '../../common/size.css';


.form {
    width: 50rem;
    margin-bottom: 2.5rem;
}

.label {
    font-size: 1.6rem;
    padding-bottom: 1rem;
}

.input, .text {
    font-size: 1.4rem;
    background-color: rgba(255, 255, 255, 0.1);
    color: #fff;
    outline: 0;
    border: 0;
    /* border: 1px solid #fff; */
    margin-bottom: 1.5rem;
    border-radius: 0.5rem;
    padding: 1rem;
}

.input::placeholder , .text::placeholder {
    color: rgba(255, 255, 255, 0.2);
    font-size: 1.4rem;
    font-weight: 300;
}

.input {
    width: 100%;
    height: 4rem;
}

.text {
    width: 100%;
    height: 15rem;
}

.button {
    width: 100%;
    font-size: 1.6rem;
    background-color: Main;
    color: #fff;
    border-radius: 0.5rem;
    padding: 1.5rem 2rem;
    margin-top: 1.5rem;

}

.button:hover {
    cursor: pointer;
    opacity: .9;
}

@media screen and (max-width: MediaMobile) {
    .form {
        width: 30rem;
        margin-bottom: 2.5rem;
    }
    
    .label {
        font-size: FontSmall;
        padding-bottom: 1rem;
    }

    .input, .text {
        font-size: 1.2rem;
    }

    .input::placeholder , .text::placeholder {
        font-size: 1rem;
    }

    .button {
        font-size: 1.4rem;
        padding: 1rem 0;
        margin-top: 1rem;
    }
}