@value Main, MainGrey, LightGrey, MainOrange from '../../common/colors.css';
@value wrapPadding, sectionTopBottom, MediaLaptop, MediaPad, MediaMobile, FontSmall, FontRegular, FontMedium , Title from '../../common/size.css';

.project {
    padding: sectionTopBottom wrapPadding;
}

.index {
    font-size: Title;
    font-weight: 500;
    color: MainGrey;
    text-align: center;
    margin-bottom: 5rem;
}

.container {
    display: flex;
    flex-direction: column;
}

@media screen and (max-width: MediaPad) {
    .project {
        padding: sectionTopBottom 5rem;
    }
}

@media screen and (max-width: MediaMobile) {
    .project {
        padding: calc(sectionTopBottom / 2) 2rem;
    }

    .index {
        font-size: calc(Title / 2);
        margin-bottom: calc(Title / 2);
    }
}
